"use client";

import React, { useMemo, useState } from "react";
import PageBreadcrumb from "@/components/common/PageBreadCrumb";
import { Modal } from "@/components/ui/modal";
import { ChevronLeft, ChevronRight, Plus, Trash2 } from "lucide-react";

type RowStatus = "WORKING" | "LEAVE" | "HOLIDAY";

interface ProjectRow {
    id: number;
    project: string;
    status: RowStatus;
    hours: number;
    overtime: number;
    notes: string;
}

interface DayEntry {
    rows: ProjectRow[];
}

const PROJECTS = ["None", "Project Alpha", "Project Beta", "Internal"];

const EMPTY_ROW = (): ProjectRow => ({
    id: Date.now() + Math.random(),
    project: "None",
    status: "WORKING",
    hours: 8,
    overtime: 0,
    notes: "",
});

function startOfWeek(date: Date) {
    const d = new Date(date);
    d.setDate(d.getDate() - d.getDay());
    d.setHours(0, 0, 0, 0);
    return d;
}

function addDays(date: Date, days: number) {
    const d = new Date(date);
    d.setDate(d.getDate() + days);
    return d;
}

export default function WeeklyTimesheetCalendar() {
    const todayWeek = startOfWeek(new Date());
    const [currentWeek, setCurrentWeek] = useState(todayWeek);

    const [entries, setEntries] = useState<Record<string, DayEntry>>({});
    const [selectedDate, setSelectedDate] = useState<string | null>(null);

    const weekDays = useMemo(() => {
        return Array.from({ length: 7 }).map((_, i) => {
            const date = addDays(currentWeek, i);
            const key = date.toISOString().split("T")[0];
            return { date, key };
        });
    }, [currentWeek]);

    const openDay = (key: string) => {
        setEntries(prev => ({
            ...prev,
            [key]: prev[key] ?? { rows: [EMPTY_ROW()] },
        }));
        setSelectedDate(key);
    };

    const closeModal = () => setSelectedDate(null);

    const updateRow = (rowId: number, updates: Partial<ProjectRow>) => {
        if (!selectedDate) return;
        setEntries(prev => ({
            ...prev,
            [selectedDate]: {
                rows: prev[selectedDate].rows.map(r =>
                    r.id === rowId ? { ...r, ...updates } : r
                ),
            },
        }));
    };

    const addRow = () => {
        if (!selectedDate) return;
        setEntries(prev => ({
            ...prev,
            [selectedDate]: {
                rows: [...prev[selectedDate].rows, EMPTY_ROW()],
            },
        }));
    };

    const removeRow = (rowId: number) => {
        if (!selectedDate) return;
        setEntries(prev => ({
            ...prev,
            [selectedDate]: {
                rows: prev[selectedDate].rows.filter(r => r.id !== rowId),
            },
        }));
    };

    const canGoNextWeek = currentWeek < todayWeek;
    const activeEntry = selectedDate ? entries[selectedDate] : null;

    return (
        <div className="p-6 space-y-6">
            <PageBreadcrumb pageTitle="Timesheet" />

            {/* Week Header */}
            <div className="flex items-center gap-4">
                <button
                    onClick={() => setCurrentWeek(addDays(currentWeek, -7))}
                    className="p-2 rounded hover:bg-gray-100"
                >
                    <ChevronLeft />
                </button>

                <h2 className="font-semibold text-lg">
                    {currentWeek.toDateString()} â€“ {addDays(currentWeek, 6).toDateString()}
                </h2>

                <button
                    disabled={!canGoNextWeek}
                    onClick={() => setCurrentWeek(addDays(currentWeek, 7))}
                    className={`p-2 rounded ${canGoNextWeek ? "hover:bg-gray-100" : "opacity-40 cursor-not-allowed"
                        }`}
                >
                    <ChevronRight />
                </button>
            </div>

            {/* Calendar */}
            <div className="grid grid-cols-7 gap-4">
                {weekDays.map(d => {
                    const entry = entries[d.key];
                    const summary =
                        entry?.rows.reduce(
                            (acc, r) => {
                                acc[r.status] = (acc[r.status] || 0) + 1;
                                return acc;
                            },
                            {} as Record<RowStatus, number>
                        ) || {};

                    return (
                        <div
                            key={d.key}
                            onClick={() => openDay(d.key)}
                            className="cursor-pointer rounded-xl border bg-white p-4 hover:shadow transition text-center"
                        >
                            <div className="text-sm text-gray-500">
                                {d.date.toLocaleDateString("default", { weekday: "short" })}
                            </div>
                            <div className="text-xl font-bold">{d.date.getDate()}</div>

                            {summary.WORKING && (
                                <div className="text-xs text-green-600">
                                    {summary.WORKING} Working
                                </div>
                            )}
                            {summary.LEAVE && (
                                <div className="text-xs text-orange-600">
                                    {summary.LEAVE} Leave
                                </div>
                            )}
                        </div>
                    );
                })}
            </div>

            {/* Modal */}
            <Modal isOpen={!!selectedDate} onClose={closeModal} className="max-w-3xl p-6">
                {activeEntry && (
                    <div className="space-y-4">
                        <h2 className="text-lg font-semibold">{selectedDate}</h2>

                        {activeEntry.rows.map(row => (
                            <div
                                key={row.id}
                                className="grid grid-cols-12 gap-2 items-center"
                            >
                                <select
                                    className="col-span-3 border rounded px-2 py-1"
                                    value={row.project}
                                    onChange={e =>
                                        updateRow(row.id, { project: e.target.value })
                                    }
                                >
                                    {PROJECTS.map(p => (
                                        <option key={p}>{p}</option>
                                    ))}
                                </select>

                                <select
                                    className="col-span-2 border rounded px-2 py-1"
                                    value={row.status}
                                    onChange={e =>
                                        updateRow(row.id, {
                                            status: e.target.value as RowStatus,
                                        })
                                    }
                                >
                                    <option value="WORKING">Working</option>
                                    <option value="LEAVE">Leave</option>
                                    <option value="HOLIDAY">Holiday</option>
                                </select>

                                <input
                                    type="number"
                                    disabled={row.status !== "WORKING"}
                                    className="col-span-2 border rounded px-2 py-1 disabled:bg-gray-100"
                                    value={row.hours}
                                    onChange={e =>
                                        updateRow(row.id, { hours: Number(e.target.value) })
                                    }
                                    placeholder="Hours"
                                />

                                <input
                                    type="number"
                                    disabled={row.status !== "WORKING"}
                                    className="col-span-2 border rounded px-2 py-1 disabled:bg-gray-100"
                                    value={row.overtime}
                                    onChange={e =>
                                        updateRow(row.id, { overtime: Number(e.target.value) })
                                    }
                                    placeholder="OT"
                                />

                                <input
                                    type="text"
                                    className="col-span-2 border rounded px-2 py-1"
                                    value={row.notes}
                                    onChange={e =>
                                        updateRow(row.id, { notes: e.target.value })
                                    }
                                    placeholder="Notes"
                                />

                                <Trash2
                                    className="col-span-1 text-red-500 cursor-pointer"
                                    onClick={() => removeRow(row.id)}
                                />
                            </div>
                        ))}

                        <button
                            onClick={addRow}
                            className="flex items-center gap-2 text-blue-600 text-sm"
                        >
                            <Plus size={16} /> Add Project
                        </button>

                        <div className="flex justify-end gap-3 pt-4">
                            <button
                                onClick={closeModal}
                                className="px-4 py-2 border rounded hover:bg-gray-100"
                            >
                                Cancel
                            </button>
                            <button
                                onClick={closeModal}
                                className="px-4 py-2 bg-blue-600 text-white rounded hover:bg-blue-700"
                            >
                                Save Day
                            </button>
                        </div>
                    </div>
                )}
            </Modal>
        </div>
    );
}
